@font-face {
  font-family: 'Avenir-Book';
  src: local('Avenir-Book'), url(./assets/fonts/Avenir-Book.ttf) format('truetype');
}

body {
  font-family: 'Avenir-Book';
}


.App {
    background-color: #B086F3;
}

.content {
  background-color: #999;
  width: 80vw;
  margin: auto;
}

.footer {
  padding-top: 10px;
  padding-bottom: 10px;
  color: #FFF;
}

@media (max-width: 767px) {
  .content {
    width: 100%;
  }
}



